* {
  box-sizing: border-box;
}

html {
  color: #2f4f4f;
  font-family: Arial, sans-serif;
  font-size: 20px;
  line-height: 23px;
}

body {
  margin: 0;
}

.people {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 220px);
  justify-content: center;
  gap: 30px 10px;
  padding: 20px 25px;
  display: grid;
  position: relative;
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

.person {
  text-align: center;
  background-color: #0000;
  position: relative;
}

.person:hover {
  z-index: 1;
}

.person:hover .photo {
  margin-bottom: 25px;
  transform: scale(1.2);
}

.person:hover .photo img {
  border-radius: 60px;
}

.person:hover:after {
  content: "";
  z-index: -1;
  background-color: #eee;
  border-radius: 10px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: scale(1.2);
}

.person:hover .extra {
  display: block;
}

.photo {
  max-width: 200px;
  margin: 0 auto;
  transition: transform 1s;
  display: block;
  position: relative;
}

.photo img {
  border-radius: 50%;
  width: 100%;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.name {
  color: #2f4f4f;
  font-weight: 700;
  line-height: 23px;
}

.extra {
  color: #000;
  margin-top: 9px;
  font-size: 18px;
  line-height: 20px;
  display: none;
}

.sport:after {
  content: ",";
}

.pay:before {
  content: "$";
}

.pay:after {
  content: "M";
}
